import { FixedDoubleAreaBridge as FixedDoubleAreaBridge } from "./bridge/fixed-area-bridge";
import { FixedDoubleSelectionBridge } from "./bridge/all-selection-bridge";
import { FixedDoubleDecorationsBridge } from "./bridge/all-decoration-bridge";
import { FixedDoubleInitializationBridge, } from "./bridge/all-initialization-bridge";
const leftIframe = document.getElementById("page-left");
const rightIframe = document.getElementById("page-right");
const metaViewport = document.querySelector("meta[name=viewport]");
Window.prototype.doubleArea = new FixedDoubleAreaBridge(window, leftIframe, rightIframe, metaViewport, window.gestures, window.documentState);
window.doubleSelection = new FixedDoubleSelectionBridge(leftIframe, rightIframe, window.doubleSelectionListener);
window.doubleDecorations = new FixedDoubleDecorationsBridge();
window.doubleInitialization = new FixedDoubleInitializationBridge(window, window.fixedApiState, leftIframe, rightIframe, window.doubleArea, window.doubleSelection, window.doubleDecorations);
window.fixedApiState.onInitializationApiAvailable();
